import React from 'react';
import s from './Contacts.css';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import imageGmail from './gmail .png';
import imageLinkedIn from './linkedin.png';
import imageFaceBook from './facebook.png';
import imageNetwork from './network.png';

class Contacts extends React.Component {
  render(){
    return(
    <div className={s.wrapperContacts}>
      <span className={s.Quotes}>
      “If I had asked people what they wanted, they would have said faster horses. ”
        - Henry Ford
      </span>
      <div className={s.linkContact}>
        <a
            target="_blank"
            href="mailto:pavlognidin@gmail.com">
              <img src={imageGmail}/>
                pavlognidin@gmail.com
        </a>
        <a
            target="_blank"
            href="facebook.com/pavlo.gnidin.3">
                <img src={imageFaceBook}/>
                facebook.com/pavlo.gnidin.3
        </a>
        <a
            target="_blank"
            href="linkedin.com/in/pasha28198/">
              <img src={imageLinkedIn}/>
              linkedin.com/in/pasha28198/
        </a>
      </div>
      <img className={s.imageNetwork} src={imageNetwork}/>
    </div>
  );

  }
}

export default withStyles(s)(Contacts);



